import { Component, Element, h, Host, Prop } from '@stencil/core';
import { SharedStyles } from '../../utils/shared-styles';

@Component({
  tag: 'storm-marquee',
  styleUrl: 'storm-marquee.css',
  shadow: true,
})
export class StormMarquee {
  @Element() host: HTMLElement;
  @Prop() width: string = '100%';
  @Prop() speed: string;

  componentWillLoad() {
    SharedStyles.applyShadowRootStyles(this.host);
  }

  componentDidRender() {
    if (!this.speed) return;
    this.host.style.setProperty('--speed', this.speed);
  }

  render() {
    return (
      <Host>
        <div class="container">
          <div class="row">
            <div class="center">
              <div class="marquee" style={{ width: this.width }}>
                <div class="inner" innerHTML={this.host.innerHTML}></div>
              </div>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
