:host {
  --speed: 30s
}

:host .marquee {
  /* width: 450px; */
  /* line-height: 50px; */
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
}

:host .inner {
  display: inline-block;
  padding-left: 100%;
  animation: marquee var(--speed) linear infinite;
}

@keyframes marquee {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-100%, 0);
  }
}

:host .center {
  display: flex;
  justify-content: center;
  align-items: center;
}